<template>
  <div>
    <Vheader :seller="seller"></Vheader>
    <!--该样式就在本vue组件里面-->
    <div class="tab">
      <div class="tab-item">
          <router-link to='/goods'>商品</router-link>
      </div>
      <div class="tab-item">
          <router-link to='/ratings'>评论</router-link>
      </div>
      <div class="tab-item">
          <router-link to='/seller'>商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import Vheader from './components/header/header'
import axios from 'axios'

const ERR_OK = 0

export default {
  data(){
    return {
      seller: {},
    }
  },
  components: {
    Vheader
  },
  created(){
    axios.get('/static/data.json').then(
      (res) => {
        this.seller = res.data.seller
      }
    )
  }
}


</script>

<style scoped>
.tab{
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #ccc;
}
.tab-item{
  flex: 1;
  text-align: center;
}
.tab-item a{
  display: block;
  font-size: 14px;
  color: rgb(77, 85, 93)
}
.tab-item a.active{
  color: rgb(240, 20, 20);
}
</style>



